<template>
    <div>
        <!--   -->
        <div class="dashang-body">
            <div class="hide_box"></div>
            <div class="shang_box">
                <a class="shang_close" @click="close()" href="javascript:void(0)" title=""><img src="static/images/dashang/close.png" alt="" /></a>
                <div class="shang_tit">
                    <p>!</p>
                </div>
                <div class="shang_payimg">
                    <img src="static/images/dashang/alipayimg.png" alt="" title="" />
                </div>
                <div class="pay_explain"></div>
                <div class="shang_payselect">
                    <div :class="cur==0?'pay_item checked':'pay_item'" data-id="alipay" @click="change('alipayimg',0)">
                        <span class="radiobox"></span>
                        <span class="pay_logo"><img src="static/images/dashang/alipay.jpg" alt="" /></span>
                    </div>
                    <div :class="cur==1?'pay_item checked':'pay_item'" data-id="weipay" @click="change('weipayimg',1)">
                        <span class="radiobox"></span>
                        <span class="pay_logo"><img src="static/images/dashang/wechat.jpg" alt="" /></span>
                    </div>
                    <div :class="cur==2?'pay_item checked':'pay_item'" data-id="qqpay" @click="change('qqpayimg',2)">
                        <span class="radiobox"></span>
                        <span class="pay_logo"><img src="static/images/dashang/qq.jpg" alt="QQ" /></span>
                    </div>
                </div>
                <div class="shang_info">
                    <p><span id="shang_pay_txt"></span></p>
                    <p></p>
                    <p><a style="margin-right:20px;color:#009BE1;font-weight:bold;" href="https://www.paypal.me/sundad" target="_blank">
                            <font color="#01315F">PayPal</font> Donation
                        </a>
                        <router-link to="/support" target="_blank" style="color:blue;"></router-link>
                    </p>
                </div>
            </div>
        </div>
        <!--   -->
    </div>
</template>

<script>
    export default {
        name: 'Demo',
        components: {},
        data() {
            return {
                cur: 0
            }
        },
        created() {},
        mounted() {},
        methods: {
            change(dataid, e) {
                $(".shang_payimg img").attr("src", "static/images/dashang/" + dataid + ".png");

                this.cur = e
                if (e == 0) {
                    $("#shang_pay_txt").text("");
                } else if (e == 1) {
                    $("#shang_pay_txt").text("");
                } else {
                    $("#shang_pay_txt").text("QQ");
                }
            },
            close() {
                $(".hide_box").fadeOut();
                $(".shang_box").fadeOut();
            }

        }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



