<template>
    <div class="page-support">
        <!--content-->
        <SupportList />
        <div class="container">
            <div class="single">
                <div class=" single-grid">

                    <div class="tit"></div>

                    <p></p>
                    <p>~</p>
                    <p></p>

                    <div class="tit"></div>
                    <div class="btn-group">
                        <a href="//shang.qq.com/wpa/qunwpa?idkey=f8918322d07e7fde0be74a718a76001e816bf04159cb60f1d990f8572b2cf2e2" title=" - GitHub:627141737" class="cd-read-more  hvr-wobble-top btn-info" target="_blank">QQ</a>
                        <a href="javascript:void(0)" title="" class="cd-read-more  hvr-wobble-top btn-info btn-dashang" @click="open()"></a>

                    </div>
                    <div class="tit"></div>
                    <table class="my-table">
                        <tr>
                            <th></th>
                            <th>()</th>
                            <th></th>
                            <th></th>
                        </tr>
                        <tr v-for="s in gameList">
                            <td>{{s.date}}</td>
                            <td>{{s.amount}}</td>
                            <td>{{s.payment}}</td>
                            <td>{{s.name}}</td>
                        </tr>
                        <tr>
                            <td colspan="2"></td>
                            <td colspan="2">{{total.toFixed(2)}} </td>
                        </tr>

                    </table>

                </div>

            </div>
        </div>

        <!--//content-->
        <!--images-->
        <div class="container">
            <div class="games">
                <!--					<div class="tit"></div>-->
                <section style="display: none">
                </section>

            </div>
        </div>
        <!--//images-->
    </div>
</template>
<script>
import SupportList from '@/components/Support.vue'
export default {
  name: 'Support',
  components: {
    SupportList
  },
  data() {
    return {
      gameList: [],
      total: 0
    }
  },
  created() {
    this.getData()
  },
  mounted() {},
  methods: {
    getData() {
      this.$http.get('static/data/support.json').then(game => {
        for (var i = 0; i < game.data.length; i++) {
          this.gameList = game.data
          this.total += game.data[i].amount
        }
      })
    },
    open() {
      $('.hide_box').fadeIn()
      $('.shang_box').fadeIn()
    }

  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



